/* global document, Office, Word */
// QUICK POC FOR TESTING THE PLUGIN CAPABILITY

var magicfield = "~$2.3M cost" //default

Office.onReady(info => {
  if (info.host === Office.HostType.Word) {
    // Determine if the user's version of Office supports all the Office.js APIs that are used in the tutorial.
    if (!Office.context.requirements.isSetSupported('WordApi', '1.3')) {
      console.log('Sorry. The tutorial add-in uses Word.js APIs that are not available in your version of Office.');
    }
    
    document.getElementById("cancel-btn").onclick = closeStoragePage;
    document.getElementById("storage_btn").onclick = openStoragePage;
    document.getElementById("submit-field").onclick = insertParagraph;
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "block";

  }
});

function closeStoragePage(){
  var page1 = document.getElementById("page1");
  var page2 = document.getElementById("page2");

  page1.classList.remove("hidden");
  page2.classList.add("hidden");
}

function openStoragePage(){
  var page1 = document.getElementById("page1");
  var page2 = document.getElementById("page2");

  page1.classList.add("hidden");
  page2.classList.remove("hidden");

}

function dropDownSelected(event){
    //var target = getEventTarget(event);
    document.getElementById("preview-msg").innerHTML = "<span>~$cccccost</span>";
    
}

function insertParagraph() {
  document.getElementById("dropdown_spend").innerHTML ="<span>TETE</span>";
  document.getElementById("preview-msg").innerHTML = "<span>~$2.3M cost</span>";
  Word.run(function (context) {
    var docBody = context.document.body;
    docBody.insertText(`~$2.3M cost`,Word.InsertLocation.end);
    return context.sync();
  })
  .catch(function (error) {
      console.log("Error: " + error);
      if (error instanceof OfficeExtension.Error) {
          console.log("Debug info: " + JSON.stringify(error.debugInfo));
      }
  });
}

